import axios from 'axios';
import { User } from './MappedDataVO';

const BASE_HOST_URL = 'https://test-web.partycity.com/s/Sites-partycity_us2-Site/dw/shop/v20_4';
const CLIENT_ID      = 'f75472a2-de04-4b73-80e1-32a261e83281';

const instance = axios.create({
  timeout: 40000,
  headers: {
    Accept: '*/*',
    'Content-Type': 'application/json',
    'x-dw-client-id': CLIENT_ID,
  },
  withCredentials: true,
});

export const createGuestAccount = async () => {
  const url = `${BASE_HOST_URL}/customers/auth`;
  
  const headers = {
    Accept: 'application/json, ',
    'Content-Type': 'application/json',
    "x-dw-client-id": CLIENT_ID,
  };

  const response = await axios.post(
    url,
    { type: "guest" },
    { withCredentials: true, headers: headers }
  );

  const user = new User(response.data);

  return {
    token: response.headers.authorization,
    user,   
  };
}

export const createGuestBasket = async (token) => {
  instance.defaults.headers.common['Authorization'] = token;
  const url = `${BASE_HOST_URL}/baskets?storeid=916`;

  const response = await instance.post(url)
  return response;
}

export const addShipping = async (params) => {
  const { token, basket_id, shipping } = params;
  instance.defaults.headers.common['Authorization'] = token;
  const url = `${BASE_HOST_URL}/baskets/${basket_id}/shipments/me`;

  const response = await instance.patch(url, shipping)
  return response;
}

export const addProductToBasket = async (params) => {
  const { token, basketID, item } = params;
  instance.defaults.headers.common['Authorization'] = token;
  const url = `${BASE_HOST_URL}/baskets/${basketID}/items?storeid=695`;

  const response = await instance.post(url, item);
  return response;
}


