import { forEach } from 'lodash';
import TokenUtil from './TokenUtil';
// import ReduxStore from '../store/ReduxStore';

/*

Factory function that takes a dictionary of {remote:local} key mappings and returns a constructor function that accepts a paramater of remote data.


USAGE EXAMPLE:
const DemoType = makeMappedKeyStruct({
  'remote_prop1':'localProp1',
  'remote_items_list':{propName:'localItems',T:LocalItemClass}, //pass this to create new typed list from an array in data
    'remote_sub_object':{'param1':'localParam', 'anotherParam':'anotherLocalParam'}, //pass like this to destructure out deeper nesting items_link.title maps to itemTitle. Right now it's only set up to work one level deep.
});

DemoType would now have 4 properties localProp1:String, localItems:Array<LocalItemClass>, localParam, anotherParam.
*/

export const makeMappedKeyStruct = (remoteToLocalKeyMappings) => {
  function constructor(remoteKeyedData) {
    //for each item in remote keyed data wer are going to remap according to remoteToLocalKeyMappings assigned in factory method above
    forEach(remoteKeyedData, (remoteValue, remoteKey) => {
      //first we grab the local key from mappings dict using remote key encountered in data
      const localKey = remoteToLocalKeyMappings[remoteKey];

      //if this localKey is found in our mapping we need to check it's types to determine how to parse remote data
      if (localKey != null || localKey != undefined) {
        //console.log(typeof localKey);

        //if local key is an object that containts a function we should create a typed list from encountered data using provided type.
        if (typeof localKey === 'object' && typeof localKey.T === 'function') {
          this[localKey.propName] = makeTypedList(localKey.T, remoteValue);

          //if it's any other object, we should look for keys to destructure
        } else if (typeof localKey === 'object') {
          //console.log('localKey is: ' , localKey);
          forEach(localKey, (localName, remoteKey) => {
            //console.log('localName: ', localName);
            this[localName] = remoteValue[remoteKey];
          });

          // finally, we assume it's a direct one to one mapping then just assign remote value to local key directly
        } else {
          this[localKey] = remoteValue;
        }
      } else {
        //console.log(`Mapping for property ${remoteKey} not found and will be skipped.`)
      }
    });
  }
  return constructor;
};

//Returns a typed list using a type T:Function  and list of data params
export const makeTypedList = function (T, list) {
  let items = [];
  list.forEach((remoteItem) => {
    const item = new T(remoteItem);
    items.push(item);
  });
  return items;
};

export const getHeaders = (ftoken) => {
  const token = TokenUtil.getInstance().getToken();

  return {
    Accept: 'application/json',
    'Content-Type': 'application/json',
    Authorization: token,
  };
};

export const getHeadersNoAuth = () => {
  return {
    Accept: 'application/json',
    'Content-Type': 'application/json',
  };
};

