export const ProductListItem = makeMappedKeyStruct({
  _type: '_type',
  id: 'id',
  priority: 'priority',
  product_details_link: { link: 'productDetailsLink', title: 'productTitle' },
  product_id: 'productId',
  public: 'isPublic',
  purchased_quantity: 'purchasedQuantity',
  quantity: 'quantity',
  type: 'type',
});

export const ProductList = makeMappedKeyStruct({
  creation_date: 'creationDate',
  customer_product_list_items: { propName: 'listItems', T: ProductListItem }, //pass this to create new typed list from an array in data
  id: 'id',
  items_link: { link: 'itemLink', title: 'itemTitle' }, //pass like this to destructure out nest properties items_link.title maps to itemTitle
  last_modified: 'lastModified',
  public: 'isPublic',
  description: 'description',
  type: 'type',
  _type: '_type',
  name: 'name',
  event: { city: 'city', country: 'country', date: 'date', state: 'state' },
});

