import React, { useEffect, useState } from 'react';
import { 
  createGuestAccount, 
  createGuestBasket, 
  addShipping 
} from '../utils/Service'
import "./Home.css"
import { Link, useHistory } from 'react-router-dom'
import { addProductToBasket } from '../utils/Service'
import Loader from "react-loader-spinner";

const Home = () => {
  const history = useHistory();
  const [loading, setLoading] = useState(true);
  // const defaultBouquet = [{ id: '909723', count: 10 }, { id: '863284', count: 2 }, { id: '889422', count: 1 }, { id: '453396', count: 2 }]

  useEffect(async () => {
    if (!localStorage.CUSTOMER_ID_KEY) {
      await createGuest();
      await createBasket();
      // await addDefaultBouquet();
      await setLoading(false);
      localStorage.INFLATE = false;
    } else {
      setLoading(false);
    };
  }, []);

  const createGuest = async () => {
    try {
      const response = await createGuestAccount();
      const token = response.token;
      const customerID = response.user.customerID;

      localStorage.ACCESS_TOKEN_KEY = token;
      localStorage.CUSTOMER_ID_KEY = customerID;
    } catch (error) {
      console.log('Create Guest Error:', error)
    }
  };

  const createBasket = async () => {
    try {
      const token = localStorage.ACCESS_TOKEN_KEY;
      const basket = await createGuestBasket(token);
      const { data } = basket;
      const { basket_id } = data;

      let shipping = {
        gift: false,
        shipment_id: 'me',
        shipping_method: {
          id: 'SNGInStore',
        },
        shipping_address: {
          first_name: 'John',
          last_name: 'Doe',
          city: 'Boston',
          country_code: 'US',
        },
      };

      localStorage.BASKET_ID_KEY = data.basket_id;
      addShipping({ token, basket_id, shipping });
    } catch (error) {
      console.log('Create Basket Error:', error)
    }
  };

  // const addDefaultBouquet = () => {
  //   const token = localStorage.ACCESS_TOKEN_KEY;
  //   const basketID = localStorage.BASKET_ID_KEY;
  //   let cart = {};

  //   defaultBouquet.map(async (balloon) => {
  //     let item = [{
  //       "product_id": balloon.id,
  //       "quantity": balloon.count,
  //     }];

  //     try {
  //       const basket = await addProductToBasket({ token, basketID, item });
  //       const { data } = basket;
  //       const { product_items, product_sub_total, order_total, tax_total } = data;

  //       const updatedCart = {
  //         product_items,
  //         product_sub_total,
  //         order_total,
  //         tax_total,
  //       };
  //       console.log('cart2', updatedCart)

  //       // await updatedCart.product_items.map(item => {
  //       //   if (item.product_id === productID.toString()) {
  //       //     item.category = categories[activeCategory].name;
  //       //     return;
  //       //   }
  //       // })

  //       return cart = updatedCart;
  //     } catch (error) {
  //       console.log('Add Item to Basket Error:', error);
  //     }
  //   })

  //   // localStorage.setItem("CART", JSON.stringify(cart));
  // }

  const handleAddClick = () => {
    if (!loading) {
      // localStorage.setItem("CART", JSON.stringify(cart));
      history.push(`/edit`);
    }
  }

  return (
    <div id="app-container">
        <div id="button-container">
          <button id="add-button" onClick={() => handleAddClick()}>
            ADD
            {/* {loading 
              ? <Loader type="Puff" color="#00BFFF" height={12} width={25} />
              : 'ADD'
            } */}
            </button>
        </div>
    </div>
  );
}

export default Home;
































